import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { ModuleList, CommonService } from '../shared/common.service';
import { AccountserviceService } from './accountservice.service';
import { ConfirmationDialogService } from '../shared/confirmation-dialog/confirmation-dialog.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { SearchfilteraddComponent } from '../shared/searchfilter/searchfilteradd.component';
import { ManageviewComponent } from '../shared/manageview/manageview.component';
import { SelectionType, DatatableComponent } from '@swimlane/ngx-datatable';
import { User } from '../manageuser/addedituser.service';


@Component({
  selector: 'app-accountslist',
  templateUrl: './accountslist.component.html',
  styleUrls: ['./accountslist.component.scss']
})
export class AccountslistComponent implements OnInit {
  @ViewChild('templateFilterView', { static: false}) FilterViewModal: SearchfilteraddComponent;
  @ViewChild('templateManageView', { static: false}) ManageViewModal: ManageviewComponent;
    @ViewChild(DatatableComponent, { static: false }) table: DatatableComponent;  
  @ViewChild('table', { static: false}) tableModal: DatatableComponent; 
  @Input() offset: number;
  moduleName = 'crm';
  submoduleName = 'account';
  custom_view_id = '';
  searchUserType = '';
  loginuserid: any;
 // modulePermission: ModuleList;
  modulePermission: any[] = [];
  ViewModel: any = '';
  //loading = false;
  //modulePermission: ModuleList;
  loadSave = false;
  SelectionType = SelectionType;
  sortDir = 'desc';
  sortColumn = 'id';
  vewId: any;
  lstUserType: any;
  pagedData: any = {
    pager: {},
    data: []
  };
  listFilter = '';
  searchTxt = '';
  listFiltertext = '';
  lstPageSize: any
  pageSizeValue: number;
  tableName = 'tblaccounts';
  jsonData: any;
  isApproveAccount = false;
  columndata: any;
  columnheading: any;
  totalRecords: any;
  currentPage: number;
  companyId : any;
  deleteId: any;
  selected = [];
  is_filter: any;
  lstListingColorCode: any;

  isAdd: boolean = false;
  isUpdate: boolean = false;
  isDelete: boolean = false;
  companyType: any;
  myCheckbox: any = false;

  constructor(private internalAccountService: AccountserviceService, private dialogService: ConfirmationDialogService,
    private commonService: CommonService, private router: Router,
    private activeRoute: ActivatedRoute
    , private toaster: ToastrService) {

   // alert(1);
    this.commonService.getMasterItemsList("usertype").subscribe((result: any) => {
      this.lstUserType = this.commonService.masters;
    })

    const moduleCode = this.activeRoute.snapshot.data.moduleCode;
    this.modulePermission = this.commonService.getPermissiondata(moduleCode);

    //console.log('modulePermission', this.modulePermission);



    let add = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'ACCOUNTADD');
    if (add == undefined) {
      add = "null";
    } else {
      this.isAdd = true;
    }
    let update = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'ACCOUNTUPDATE');
    if (update == undefined) {
      update = "null";
    } else {
      this.isUpdate = true;
    }

    let deletedata = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'ACCOUNTDELETE');
    if (deletedata == undefined) {
      deletedata = "null";
    } else {
      this.isDelete = true;
    }

    this.commonService.getLoggedInName.subscribe((userdetail: any) => {
      this.loginuserid = userdetail.id;
      this.companyId = userdetail.companyId;
    });




    this.commonService.getLoggedInName.subscribe((userdetail: any) => {
      this.loginuserid = userdetail.id;
      this.companyId = userdetail.companyId;
      this.companyType = userdetail.companyType;
    });

    this.commonService.getMasterItemsList("usertype").subscribe((result: any) => {
      this.lstUserType = this.commonService.masters;
    })
  }

  ngOnInit() {
    this.custom_view_id = '';
    this.pageSizeValue = 15;
    this.currentPage = 1;
    this.getPageSizes();
    this.LoadViewData();
    this.refresh();
  }




  ApplyAdvanceFilter(event) {
    this.currentPage = 1;
    this.listFiltertext = '';
    this.listFiltertext = event;
    this.refresh();
  }

  GetcustomViewid(event) {
    if (event == 'undefined' || typeof event == 'undefined') {
      //this.isAddViewFirstTime = true;
      this.LoadViewData();
    }
    this.pagedData.data.forEach(cnt => {
      if (cnt.custom_view_id == event) {
        this.ViewModel = cnt.view_name;
      }
    });
    this.vewId = event;
    this.custom_view_id = event;
    this.refresh();
  }
  getListingColorCode(fieldValue: any) {
    this.lstListingColorCode = '';
    if (fieldValue != null) {
      this.lstListingColorCode = fieldValue.split('::');
      if (this.lstListingColorCode.length > 0) {
        this.lstListingColorCode = [{ color: this.lstListingColorCode[0], colorkey: this.lstListingColorCode[1] }]
      }
    }
    return this.lstListingColorCode;
  }
  refresh() {
    this.selected = [];
    this.deleteId = null;
    this.loadSave = true;
    this.internalAccountService.GetAccountsList(this.listFiltertext, this.sortColumn, this.currentPage, this.pageSizeValue, this.sortDir, this.loginuserid, this.moduleName, this.submoduleName, this.companyId, this.custom_view_id, this.myCheckbox)
      .subscribe(response => {
       
        this.jsonData = response;
        this.columndata = this.jsonData.data;
        this.columnheading = this.jsonData.schema;
       // console.log("this.columnheading", this.columnheading); 
        this.columnheading.forEach(function (element) {
          element["colwidth"] = (String(element.DISPLAY_NAME).split(' ').join('_').length * 12) + 12;
       //   this.tableModal.width = (String(element.DISPLAY_NAME).split(' ').join('_').length * 12) + 12;
        });

        if (response.data.length > 0) {
          this.totalRecords = this.columndata[0].total_records;
          // console.log("Data", this.jsonData.schema);
        } else {
          this.totalRecords = 0;
        }
        this.offset = this.currentPage;
        this.loadSave = false;
      }, error => {
          this.loadSave = false;
      });

  }

  get curPage(): number {
    return this.offset;
  }

  getPageSizes() {
    this.commonService.getMasterItemsList("PageSize").subscribe((res: any) => {
      this.lstPageSize = this.commonService.masters;
    })
  }

  setPage($event) {
    this.loadSave = true;
    this.currentPage = $event.page;
    this.refresh();
  }

  onSort($event) {
    const sort = $event.sorts[0]
    this.sortDir = sort.dir;
    this.sortColumn = sort.prop;
    this.currentPage = 1;
    this.refresh();
  }
  onChange($event) {
    this.currentPage = 1;
    this.refresh();
  }
  displayDetailDetail(TemplateName: any) {
    this.ManageViewModal.show(TemplateName);
  }

  updateFilter(event) {
    this.searchTxt = event.target.value;
    let keycode = (event.keyCode ? event.keyCode : event.which);
    if (keycode === 13 || keycode === '13') {
      this.search();
    }
  }
  search() {
    this.currentPage = 1;
    this.listFiltertext = '';
    if (this.listFilter.trim().length > 0) {
      this.listFiltertext = "Name like '%" + this.listFilter + "%'";
    }
    this.refresh();
  }
  reset() {
    this.table.selected = [];
    this.listFilter = '';
    this.listFiltertext = '';
    this.currentPage = 1;
    this.myCheckbox = false;
    this.refresh();
  }
  onSelect({ selected }) {
    if (this.deleteId == "" || this.deleteId == null || this.deleteId == 'undefined') {
      this.selected.splice(0, this.selected.length);
      this.selected.push(...selected);
      this.deleteId = null;
      this.deleteId = "";
      for (let i = 0; i < selected.length; i++) {
        this.deleteId += selected[i].Id.toString() + ",";
      }

    }
    else {
      this.deleteId = null;
      this.deleteId = "";
      for (let i = 0; i < selected.length; i++) {
        this.deleteId += selected[i].Id.toString() + ",";
      }
    }
  }

  popUpOpen() {
    this.is_filter = '';
    this.is_filter = this.listFiltertext.length;
    this.FilterViewModal.show(this.companyId, this.is_filter);
  }

  deleteAll() {
    if (this.deleteId != null && this.deleteId != "") {
      const message = `Are you sure you want to delete the selected account(s)?`;
      this.dialogService.confirm('Delete Account(s)', message).subscribe(confirmed => {
        if (confirmed) {
          this.internalAccountService.DeleteRecords(this.deleteId, this.tableName).subscribe(r => {
            this.toaster.success(`Record(s) has been deleted successfully`);
            this.deleteId = "";
            this.selected = [];
            this.refresh();
          }, error => {
          });
        }
      });
    }
    else {
      this.toaster.error("Please select at least one row.");
    }
  }

  Delete(row: any) {
    const message = `Are you sure you want to delete Account "${row.Name}"?`;
    this.dialogService.confirm('Delete Account', message).subscribe(confirmed => {
      if (confirmed) {
        this.internalAccountService.DeleteRecords(row.Id, this.tableName).subscribe(r => {
          this.toaster.success(`Account "${row.Name}" has been deleted successfully`);
          this.refresh();
        }, error => {
        });
      }
    });
  }
  ApproveAccount(row: any) {
    const message = `Are you sure you want to Approve Account "${row.Name}"?`;
    this.dialogService.confirm('Approve Account', message).subscribe(confirmed => {
      if (confirmed) {
        this.internalAccountService.AppeoveAccount(row.Id, this.tableName).subscribe(r => {
          // console.log("approve account", this.internalAccountService.editPage);
          if (this.internalAccountService.editPage.statusCode == 200) {
            this.toaster.success(`Account "${row.Name}" has been approved successfully`);
            this.refresh();
            this.isApproveAccount = true;
          }
          else {
            this.toaster.error(this.internalAccountService.editPage.responseMessage);
            this.refresh();
          }
        }, error => {
        });
      }
    });
  }
  LoadViewData() {
    this.loadSave = true;
    this.pageSizeValue = 15;
    this.currentPage = 1;
    this.internalAccountService.getViewList(this.searchTxt, this.searchUserType, this.sortColumn, this.sortDir, 0, this.pageSizeValue, this.moduleName, this.submoduleName, this.companyId).subscribe(response => {
      this.pagedData = this.internalAccountService.pagedData;
      this.pagedData.data.forEach(cnt => {
        if (cnt.is_default == true) {
          this.vewId = cnt.custom_view_id;
          this.ViewModel = cnt.view_name;
        }
      });

      this.loadSave = false;
    }, error => {
      this.loadSave = false;
    })
  }

  SetManageViewValue(event, text) {
    this.ViewModel = text;
    this.custom_view_id = event;
    this.refresh();
    //this.LoadViewData();
  }
  switchClicked(event) {
    this.listFiltertext = '';
    this.myCheckbox = event.srcElement.checked;
    this.currentPage = 1;
    if (this.listFilter.trim().length > 0) {
      this.listFiltertext = "Name like '%" + this.listFilter + "%'";
    }
    if (this.listFiltertext.trim().length > 0) {
      this.refresh();
    }
    if (this.myCheckbox == false) {
      this.refresh();
    }
  }
}
